	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.abendblatt.de/contents/ha/news/lokales/html/140102/1214BENA0.HTM\">Hamburger Abendblatt</a>: A pair of brothers made fools of Hamburg justice officials. The younger brother was a pimp sentenced to 2 years prison. His older brother was broke and offered to take his place. Prison officials didn't notice the switch for over 10 months, even though the brother in the cell was a head taller and 20 kg heavier than the brother that was convicted.</p>","fields":{"title":"Brother Can You Do Some Time?","date":"14 Jan 2002","url":"https://papascott.de/2002-01-14-brother-can-you-do-some-time/"}}},"pathContext":{"slug":"/2002-01-14-brother-can-you-do-some-time/","prev":{"fields":{"title":"The Semmelings","date":"13 Jan 2002","slug":"/2002-01-13-the-semmelings/"}},"next":{"fields":{"title":"Duel of the German Directors","date":"14 Jan 2002","slug":"/2002-01-14-duel-of-the-german-directors/"}}}}

